import React from 'react';
import ReactDOM from 'react-dom/client';
//import App from './App';
//import "bulma/css/bulma.css";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import UserList from "./components/UserList/UserList";
import ProveedoresListado from "./components/ProveedoresListado/ProveedoresListado";
import Header from "./components/Header/Header";
import Footer from "./components/Footer/Footer";
import MenuPrincipal from "./components/MenuPrincipal/MenuPrincipal";
import MenuSecundario from "./components/MenuSecundario/MenuSecundario";
import Contenido from './components/Contenido/Contenido';

const root = ReactDOM.createRoot(document.getElementById('root'));
export default function App() {
  return (
    <BrowserRouter>
      <MenuPrincipal/>
      <MenuSecundario/>
      <Routes>
          <Route path="catalogoitems" element={<UserList />} />
          <Route path="proveedores" element={<ProveedoresListado />} />
          <Route path="ordenescompra" element={<Contenido />} />
      </Routes>
      <Footer/>
    </BrowserRouter>
  );
}
root.render(
  
  
    <App />
  
);
