import React, { useState, useEffect } from "react";
import axios from "axios";
import ReactPaginate from "react-paginate";
import Siguiente from '../../assets/der.png'
import Anterior from '../../assets/izq.png'
import './UserList.css'

const UserList = () => {
  const [users, setUsers] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [pages, setPages] = useState(0);
  const [rows, setRows] = useState(0);
  const [keyword, setKeyword] = useState("");
  const [query, setQuery] = useState("");
  const [validacion, setValidacion] = useState(false);
  const isValidacionDato = /^[a-z A-Z 0-9   .]{4,50}$/;

  useEffect(() => {
    document.title = "Sitio Oficial del Municipio de General Pueyrredn | Catlogo de bienes";  
  }, []);

  useEffect(() => {
    if (keyword != ""){
      getUsers();
    }
  }, [page, keyword]);

  const validacionDato = (value) => {
    if (!isValidacionDato.test(value)) {
      setValidacion(false);
    } else {
      setValidacion(true);
    } 
  };

  const getUsers = async () => {
    try {
	const response = await axios.get(
	      //`http://localhost:5000/users?search_query=${keyword}&page=${page}&limit=${limit}`
	      //`https://rafamback.mardelplata.gob.ar/catalogoitem?search_query=${keyword}&page=${page}&limit=${limit}`
        //`http://localhost:8080/catalogoitem?search_query=${keyword}&page=${page}&limit=${limit}`
		`/catalogoitem?search_query=${keyword}&page=${page}&limit=${limit}`
    //`http://localhost:5000/catalogoitem?search_query=${keyword}&page=${page}&limit=${limit}`
	    );
        setUsers(response.data.result);
        setPage(response.data.page);
        setPages(response.data.totalPage);
        setRows(response.data.totalRows);
    
    } catch (error) {
      if (error.response){
        console.log('data: ', error.response.data);
        console.log('status: ', error.response.status);
        console.log('headers: ', error.response.headers);
        console.log('Solicitud realizada pero el servidor respondi con un error');
    } else if (error.request){
        console.log(error.request);
        console.log('Solicitud realizada pero no se recibe respuesta del servidor.');
    } else {
        console.log('Error', error.message);
    }
  }};

  const changePage = ({ selected }) => {
    setPage(selected);
  };

  const searchData = (e) => {
    e.preventDefault();
    setPage(0);
    setKeyword(query);
  };

  const onChange = (e) => {
    setQuery(e.target.value);
    validacionDato(e.target.value);
  };

  const resetInput = () => {
    setQuery("");
    setValidacion("");
    setUsers("");
    setPages("");
    setKeyword("");
  };

//------------------------Expand de la table //

  const [expandedRows, setExpandedRows] = useState([]);

//Esta funcin se llama cuando se hace clic en mostrar/ocultar enlace. 
  const handleEpandRow = (event, userId) => {
    const currentExpandedRows = expandedRows;
    const isRowExpanded = currentExpandedRows.includes(userId);

    const obj = {};
    isRowExpanded ? (obj[userId] = false) :  (obj[userId] = true);

// Si la fila est expandida, estamos aqu para ocultarla.  (No me cierran estas lineas del todo)
  const newExpandedRows = isRowExpanded ?
    currentExpandedRows.filter(id => id !== userId) :
    currentExpandedRows.concat(userId);
    setExpandedRows(newExpandedRows);
  }

  return (
    <div className="contenidoUser">
          <form onSubmit={searchData}>
                <input type="text" value={query} onChange={onChange}  onKeyDown={(e) => {e.key === 'Enter' && e.preventDefault(); }} />
                <button type="submit" className={`${validacion ? "success" : "error"}`}>Buscar</button>
                <button className="reset" onClick={resetInput}> Reset </button>
          </form>
          <p className={`${validacion ? "success" : "error"}`}> Ingrese minimo 4 caracteres y/o caracteres validos </p>
<div className="scroll">
          <table className="tabla">
            <thead className="tableHead">
              <tr>
                <th>Cdigo</th>
                <th>Clase</th>
                <th>Item</th>
                <th></th>
              </tr>
            </thead>
            {users.length === 0 ? (
          <tbody>
            <tr>
              <td colSpan={4} className="text-center mb-0"></td>
            </tr>
          </tbody>
            ):(
            users.map((user) => (
            <tbody>
                <tr key={user.cod}>
                  <td>{user.cod}</td>
                  <td>{user.nomClase}</td>
                  <td className="catItem">{user.nomItem}</td>
                  <td>
                  <button className="botonExpand" onClick={event => handleEpandRow(event, user)}>Detalle </button>
                  </td>
                </tr>
                {
                  expandedRows.includes(user) ?
                  <tr className="expand">
                    <td colspan="4" >
                      <tr><td><b>Clase Nro.: </b>{user.claseDetalle}</td></tr>
                      <tr><td><b>Tipo : </b>{user.tipoDetalle}</td></tr>
                    </td>
                  </tr> : null
                }            
              </tbody>)))}
          </table>
</div>
<p className="datosTexto" ></p>

         <nav className="pagination is-centered espacio" role="navigation" aria-label="pagination">
        
            <ReactPaginate
              previousLabel={<img src={Anterior}/>}
              nextLabel={<img src={Siguiente}/>}
              pageCount={Math.min(10, pages)}
              onPageChange={changePage}
              containerClassName={"pagination-list"}
              pageLinkClassName={"pagination-link"}
              previousLinkClassName={"pagination-previous"}
              nextLinkClassName={"pagination-next"}
              activeLinkClassName={"pagination-link is-current"}
              disabledLinkClassName={"pagination-link is-disabled"}
            />

          </nav>
    </div>
  );
};

export default UserList;
