import React from 'react'
import Nav from 'react-bootstrap/Nav';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../MenuPrincipal/MenuPrincipal.css'


function MenuPrincipal() {
  return (
  <>
    <Navbar collapseOnSelect expand="lg" bg="ligth" className="navbar-principal p-2">
      <Container className="navFlex">
        <Navbar.Brand> 
          <a href="https://www.mardelplata.gob.ar/" name="Pagina de Inicio de la Municipalidad de General Pueyrredon">
            <span class="sr-only">Municipalidad de General Pueyrredon</span>
            <img src="https://www.mardelplata.gob.ar/sites/all/themes/mgp/assets/img/mgp-logo-color.png" alt="MGP" className="fotoMenu" title=" "/>
          </a>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
          </Nav>
          <Nav  className="right ajustes">
          <ul className="desplegable">
            <li><a href="https://www.mardelplata.gob.ar/transparencia">Transparencia</a></li>
            <li><a href="https://www.mardelplata.gob.ar/noticias">Noticias</a></li>
          </ul>
          <ul className="iconosMedia">
            <li><a href="https://www.facebook.com/municipalidadmardelplata/" target="_blank" name="Facebook"><i className="fa fa-facebook" aria-hidden="true"></i></a></li>
            <li><a href="https://www.twitter.com/MGPmardelplata" target="_blank" name="Twitter"><i className="fa fa-twitter" aria-hidden="true"></i></a></li>
            <li><a href="https://www.youtube.com/user/munimardelplata" name="Youtube"><i className="fa fa-youtube" aria-hidden="true"></i></a></li>
            <li><a href="https://www.instagram.com/munimardelplata/" name="Instagram"><i className="fa fa-instagram" aria-hidden="true"></i></a></li>
          </ul>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar> 
  </>
   
  )
}

export default MenuPrincipal