import React, { useState, useEffect } from "react";
import axios from "axios";
import ReactPaginate from "react-paginate";
import Siguiente from '../../assets/der.png';
import Anterior from '../../assets/izq.png';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import Modal from 'react-bootstrap/Modal';
import Moment from 'react-moment';
import 'moment/locale/es';//opcional para que muestre la fecha en espaol
import './Contenido.css';


const Contenido = () => {
  const [ordenes, setOrdenes] = useState([]);
  const [keyword, setKeyword] = useState("");
  const [contrataciones, setContratacion] = useState([]);
  const [selectedContrataciones, setSelectedContrataciones] = useState('');
  const [anio, setAnio] = useState ("");
  const [jurisdicciones, setJurisdicciones] = useState([]);
  const [selectedJurisdiccion, setSelectedJurisdiccion] = useState('');
  const [searched, setSearched] = useState(false);
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(0);
  const [pages, setPages] = useState(0);
  const [limit, setLimit] = useState(10);
  const [rows, setRows] = useState(0);
  const [currentPage, setCurrentPage] = useState ('');

  //>>>>>>>>>>>>>Peticion<<<<<<<<<<<<<<<<<<<<<<<

const searchOrdenes = async (selectedPage) => {
    try {
      
      if (anio === '' || (parseInt(anio) >= 2007)){
      setLoading(true);
      const response = await axios.get(`/ordencompra?search_query=${keyword}&anio=${anio}&jurisdiccion=${selectedJurisdiccion}&tipo_cont=${selectedContrataciones}&nro_oc=${keyword}&page=${selectedPage}&limit=${limit}`);
      setOrdenes(response.data.result);
      setPage(response.data.page);
      setPages(response.data.totalPage);
      setRows(response.data.totalRows);
      setSearched(true);
      setLoading(false);
      }else {
        // Manejar el caso en el que el ao ingresado no cumpla la condicin
        setError('El ao debe ser mayor o igual a 2007');
        // Puedes mostrar un mensaje de error al usuario o realizar alguna otra accin
        setTimeout(() => {
          setError('');
        }, 3200);
      }
      
    } catch (error) {
      if (error.response){
        console.log('data: ', error.response.data);
        console.log('status: ', error.response.status);
        console.log('headers: ', error.response.headers);
        console.log('Solicitud realizada pero el servidor respondi con un error.');
       
      } else if (error.request){
        console.log(error.request);
        console.log('Solicitud realizada pero no se recibe respuesta del servidor.');
      } else {
        console.log('Error', error.message);
      }
    }};


  const handleSearch = () => {
    setLimit(10);
    setCurrentPage(0);
    searchOrdenes(0);
  };

  const resetInput = () => {
    setKeyword("");
    setAnio("");
    setRows("");
    setPage(0);
    setPages(0);
    setLimit(5);
    setOrdenes(0);
    setSelectedJurisdiccion("");
    setSelectedContrataciones("");
    setLoading(false);
  };

  //-----------------------------Paginador-------------------------------//

  const handlePageClick = ({ selected }) => {
      setCurrentPage(selected); // Actualizar la pgina actual
      searchOrdenes(selected); // Llamar a searchFoodTrucks con la pgina actual
    };


//---------------------------------------Modal Fullscreen-----------------------------------//

//------------------------------MODAL----------------------------------//
const [showModal, setShowModal] = useState(false);
const [fullscreen, setFullscreen] = useState(true);
const [selectedOrden, setSelectedOrden] = useState(null);


const [infoOrden, setInfoOrden] = useState(null);

const getInfoOrden = async (nro_oc, ejercicio) => {
  try {
    const response = await axios.get(`/ordencompra/getdetails?nro_oc=${nro_oc}&ejercicio=${ejercicio}`);
    setInfoOrden(response.data.result); // Ajustar el acceso a los datos
  
  } catch (error) {
    console.error(error);
  }
};

const handleModalShow = (orden) => {
  setSelectedOrden(orden);
  getInfoOrden(orden.nro_oc, orden.ejercicio);
  setShowModal(true);
};

const handleModalClose = () => {
  setShowModal(false);
};

const OrdenModal = ({ show, handleClose, orden }) => {
    if (!orden) {
      return null;
    }
  
    return (
      <Modal show={show} fullscreen={fullscreen} onHide={handleClose} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>Nro. O.C.: {orden.nro_oc}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
                             
                        <div className="display">
                          <h5><b className="textoModal">Proveedor: </b>{orden.razon_social}</h5><br/>
                        </div>
                        <div className="display espacio">
                          <h5><b className="textoModal">  Observaciones: </b>{orden.observaciones}</h5><br/>
                        </div>
                        <div className="display espacio">
                          <h5><b className="textoModal">  Cdigo Tipo de contratacin: </b>{orden.tipo_cont}</h5><br/>
                        </div>
                        <div className="display espacio">
                          <h5 ><b className="textoModal">Descripcin Tipo de contratacin: </b>{orden.tipo_cont_desc}</h5><br/>
                        </div>
                        <div className="display espacio">
                          <h5 ><b className="textoModal">Confirmado: </b>{orden.confirmado === "S" ? "S" : "No"} </h5><br/>
                        </div>

                        <table className="tabla">
                          <thead className="tableHead">
                            <tr>
                              <th>Item OC</th>
                              <th>Descripcion</th>
                              <th>Imp. Unitario</th>
                              <th>Cantidad</th>
                              <th>Unidad Medida</th>
                              <th>Total</th>
                            </tr>
                          </thead>
                          {infoOrden && infoOrden.length > 0 && (
  <tbody>
    {infoOrden.map((ordenDetalle) => (
      <tr> 
        <td>{ordenDetalle.item_OC}</td>
        <td>{ordenDetalle.descripcion}</td>
        <td>${ordenDetalle.imp_Unitario}</td>
        <td>{ordenDetalle.cantidad}</td>
        <td>{ordenDetalle.uniMed}</td>
        <td>${ordenDetalle.total}</td>
      </tr>
    ))}
  </tbody>
)}
                        </table>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={handleClose}>
            Cerrar
          </Button>
        </Modal.Footer>
      </Modal>
    );
  };


 //--------------------------Fin Modal------------------------------------------------//

//--------------------------------Peticion Jurisdiccion------------------------------------------------------//

const URLPeticion = `/jurisdiccion` 


useEffect( ()=>{
        getFoodIdJurisdiccion()
    },[])

    const getFoodIdJurisdiccion = async () => {
        try {
        const res = await axios.get(URLPeticion)
        const data = res.data;

        // Almacena los datos en el estado de categorias
        setJurisdicciones(data);
      } catch (error) {
        console.error('Error al obtener las categoras:', error);
      }

    }
    const handleJurisdiccionChange = (event) => {
        setSelectedJurisdiccion(event.target.value);
      };       


//--------------------------------------------------------------------------------------------//

//--------------------------------Peticion Contrataciones-------------------------------------//

const URLPeticionContrataciones = `/tipocontratacion` 


useEffect( ()=>{
        getFoodIdContrataciones()
    },[])

    const getFoodIdContrataciones = async () => {
        try {
        const res = await axios.get(URLPeticionContrataciones)
        const data = res.data;

        // Almacena los datos en el estado de categorias
        setContratacion(data);
      } catch (error) {
        console.error('Error al obtener las categoras:', error);
      }

    }
    const handleContratacionesChange = (event) => {
        setSelectedContrataciones(event.target.value);
      };       


//--------------------------------------------------------------------------------------------//

return (
    <>
    <div className="head">
        <div className="content container backHead">
          <h2 className="">Resumen de ordenes de Compra</h2>
        </div>
    </div>
    <div className="contenido">
        <Form.Control type="number" className="Inputs  col-md-2 col-lg-2" placeholder="Ingrese Nro. de Orden" value={keyword} onChange={(e) => setKeyword(e.target.value)} />
        <Form.Control  type="number" className="Inputs col-md-2 col-lg-2" placeholder="Ao" maxLength={4} value={anio}  onChange={(e) => setAnio(e.target.value)} /> 
        <div className="col-md-3 col-lg-3 Inputs">
        <Form.Select value={selectedJurisdiccion} onChange={handleJurisdiccionChange} required>
                <option value="">-- Seleccione jurisdicciones --</option>
                {jurisdicciones.map((jurisdiccion) => (
                    <option value={jurisdiccion.jurisdiccion}> {jurisdiccion.denominacion}</option>
                ))}
        </Form.Select>
      </div>
      <div className=" col-md-3 col-lg-3 Inputs">
        <Form.Select value={selectedContrataciones} onChange={handleContratacionesChange} required>
          <option value="">-- Seleccione contratacion --</option>
            {contrataciones.map((contratacion) => (
              <option value={contratacion.cod_cont}> {contratacion.descripcion}</option>
            ))}
        </Form.Select>
      </div>
   
        <Button variant="success" type="submit" onClick={handleSearch}> Buscar </Button>
        <Button variant="danger" onClick={resetInput} className="Inputs "> Reset </Button>
      </div>

      <div className="contenedorAlert">
        {error && <Alert variant="danger" className="alerta">{error}</Alert>}
      </div>
      {loading && (
      <div class="loading">
      <span class="loader"></span>
      </div>
      )}

      <div className="scroll">
        <table className="tabla">
          <thead className="tableHead">
            <tr>
              <th>Jurisdiccin</th>
              <th>Dependencia</th>
              <th>Nro. O.C.</th>
              <th>Fecha emisin</th>
              <th>Ejercicio</th>
              <th>Importe </th>
              <th>Estado</th>
              <th></th>
            </tr>
          </thead>
          {searched && ordenes.length === 0 && (
      <p>Resultado no encontrado</p>
      )}
    {ordenes.length > 0 && (
      ordenes.map((orden) => (
          <tbody>
            <tr>
              <td className="jurisdiccion">{orden.jurisdiccion}</td>
              <td className="jurisdiccion">{orden.descripcion}</td>
              <td>{orden.nro_oc}</td>
              <td><Moment format="DD/MM/YYYY">{orden.fech_oc}</Moment></td>
              <td>{orden.ejercicio}</td>
              <td>${orden.importe_tot}</td>
              <td >{orden.estado_oc}</td>
              <td><Button variant="outline-info" className="boton" onClick={() => handleModalShow(orden)}>+ Ver Mas </Button> </td>
            </tr>
          </tbody>
          ))
          )}
        </table>
        <OrdenModal
          show={showModal}
          handleClose={handleModalClose}
          orden={selectedOrden}
        />
      </div>
      <nav className="pagination is-centered espacio" role="navigation" aria-label="pagination">
      
      <ReactPaginate
        previousLabel={<img src={Anterior}/>}
        nextLabel={<img src={Siguiente}/>}
        pageCount={Math.min(10, pages)}
        onPageChange={handlePageClick}
        forcePage={currentPage} // Pasar el nmero de pgina actual como valor para forcePage
        containerClassName={'pagination'}
        activeClassName={'active'}
        pageLinkClassName={""}
        previousLinkClassName={"pagination-previous"}
        nextLinkClassName={"pagination-next"}
        activeLinkClassName={""}
        disabledLinkClassName={"pagination-link is-disabled"}
      />
</nav>

    </>
  );
}

export default Contenido